body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.body{
    background:#9b2727;
}


.homeBodyContent {
    min-height:100vh;
    height:100%;
    margin-bottom:-44px;
    background:#9b2727;
   }
  
   h4{
       padding-top:25px;
       color:#fff;
   }

   .logoLg{
    margin-left:-15px;
   }

   .jumbotron{
    background:#9b2727;
    margin-bottom:1em;
    padding:4em 1em 1em 1em;
   }

  .tab-content{
      background:#fff;
      padding:60px;
      border-bottom-left-radius:.5rem;
      border-bottom-right-radius:.5rem;
      border:1px solid #9b2727;
      border-top:0;
  }

  .tab-col{
      margin:0px auto;
      box-shadow: 0 3px 12px rgba(0,0,0,.26), 0 3px 12px rgba(0,0,0,.33);
      padding-left:0;
      padding-right:0;
      margin-bottom:120px;
  }

 .nav-tabs .nav-link{
      width:50%;
      font-weight:bold;
      padding:20px 30px;
      font-size:1.3em;
  }


  .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link{
    border-radius:0;
    border-bottom:none;
    background:#9b2727;
      border-color:#9b2727;
      color:#fff;
}

  .nav-tabs .nav-link:focus, .nav-tabs .nav-link:hover{
      border-color:none;
      color:#dee2e6;
      border-color:#9b2727;
  }

  .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active{
    background:#fff;
    border-color:#fff;
    color:#9b2727;
      border-color:#9b2727;

  }



  
@media (max-width: 576px){
    .home-container{
       padding:2em 1em;
    }
    
  } 
